import { useState, useEffect } from "react";
import { CopilotSidebar, CopilotKitCSSProperties } from "@copilotkit/react-ui";
import { useCopilotAction } from "@copilotkit/react-core";
import { MessageSquare, Settings, Activity, Database } from "lucide-react";

export default function HomePage() {
  const [backendStatus, setBackendStatus] = useState<'connected' | 'disconnected' | 'loading'>('loading');
  const [backendActions, setBackendActions] = useState<any[]>([]);
  const [debugInfo, setDebugInfo] = useState<any>(null);

  // 检查后端连接状态
  useEffect(() => {
    const checkBackendStatus = async () => {
      try {
        const response = await fetch('http://localhost:3001/health');
        if (response.ok) {
          setBackendStatus('connected');
          const health = await response.json();
          setDebugInfo(health);
        } else {
          setBackendStatus('disconnected');
        }
      } catch (error) {
        setBackendStatus('disconnected');
      }
    };

    const fetchActions = async () => {
      try {
        const response = await fetch('http://localhost:3001/api/actions');
        if (response.ok) {
          const data = await response.json();
          setBackendActions(data.actions);
        }
      } catch (error) {
        console.error('获取 Actions 失败:', error);
      }
    };

    checkBackendStatus();
    fetchActions();

    // 每30秒检查一次状态
    const interval = setInterval(checkBackendStatus, 30000);
    return () => clearInterval(interval);
  }, []);

  // 注册前端 Action 来测试工具调用
  useCopilotAction({
    name: "showNotification",
    description: "显示前端通知消息",
    parameters: [
      {
        name: "message",
        description: "通知消息内容",
        type: "string",
        required: true,
      },
      {
        name: "type",
        description: "通知类型: success, error, warning, info",
        type: "string",
        required: false,
      },
    ],
    handler: ({ message, type = "info" }: { message: string; type?: string }) => {
      alert(`${type.toUpperCase()}: ${message}`);
      return `已显示通知: ${message}`;
    },
  });

  return (
    <div 
      className="flex h-screen bg-gradient-to-br from-blue-50 to-indigo-100"
      style={{
        "--copilot-kit-primary-color": "#3b82f6",
      } as CopilotKitCSSProperties}
    >
      {/* 主聊天区域 */}
      <div className="flex-1 flex flex-col">
        <header className="bg-white shadow-sm border-b p-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <MessageSquare className="text-blue-600" size={24} />
              <h1 className="text-xl font-semibold text-gray-800">
                CopilotKit Debug Example (DeepSeek + Vite)
              </h1>
              <span className={`status-indicator ${backendStatus}`}></span>
              <span className="text-sm text-gray-600">
                后端状态: {backendStatus === 'connected' ? '已连接' : backendStatus === 'disconnected' ? '断开连接' : '连接中...'}
              </span>
            </div>
            <div className="text-sm text-gray-500">
              可用 Actions: {backendActions.length}
            </div>
          </div>
        </header>

        <main className="flex-1 p-6">
          <div className="max-w-4xl mx-auto">
            <div className="bg-white rounded-lg shadow-lg p-6 mb-6">
              <h2 className="text-lg font-semibold mb-4 flex items-center">
                <Settings className="mr-2" size={20} />
                调试信息
              </h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {/* 后端状态 */}
                <div className="debug-panel p-4">
                  <h3 className="flex items-center">
                    <Activity className="mr-2" size={16} />
                    后端健康状态
                  </h3>
                  {debugInfo ? (
                    <pre className="text-xs">{JSON.stringify(debugInfo, null, 2)}</pre>
                  ) : (
                    <p className="text-gray-500">获取中...</p>
                  )}
                </div>

                {/* 可用 Actions */}
                <div className="debug-panel p-4">
                  <h3 className="flex items-center">
                    <Database className="mr-2" size={16} />
                    可用 Actions
                  </h3>
                  {backendActions.length > 0 ? (
                    <div className="space-y-2">
                      {backendActions.map((action: any, index: number) => (
                        <div key={index} className="border rounded p-2 bg-gray-50">
                          <div className="font-medium text-sm">{action.name}</div>
                          <div className="text-xs text-gray-600">{action.description}</div>
                          <div className="text-xs text-gray-500">
                            参数: {action.parameters?.length || 0}
                          </div>
                        </div>
                      ))}
                    </div>
                  ) : (
                    <p className="text-gray-500">暂无可用 Actions</p>
                  )}
                </div>
              </div>
            </div>

            {/* 使用说明 */}
            <div className="bg-white rounded-lg shadow-lg p-6">
              <h2 className="text-lg font-semibold mb-4">🔍 调试说明</h2>
              <div className="space-y-4 text-sm text-gray-600">
                <div>
                  <h3 className="font-medium text-gray-800 mb-2">后端调试:</h3>
                  <ul className="list-disc list-inside space-y-1">
                    <li>在 VS Code 中打开 <code className="bg-gray-100 px-1 rounded">debug-example/backend/src/server.ts</code></li>
                    <li>设置断点在 CopilotRuntime 相关代码中</li>
                    <li>运行 <code className="bg-gray-100 px-1 rounded">npm run debug</code> 启动调试模式</li>
                    <li>在浏览器中发送消息，触发断点进行调试</li>
                  </ul>
                </div>
                <div>
                  <h3 className="font-medium text-gray-800 mb-2">测试 Actions:</h3>
                  <ul className="list-disc list-inside space-y-1">
                    <li>询问当前时间: "现在几点了？"</li>
                    <li>数学计算: "帮我计算 2 + 3 * 4"</li>
                    <li>查询用户: "查询用户ID为1的信息"</li>
                    <li>运行时状态: "获取运行时调试状态"</li>
                    <li>前端通知: "显示一个成功通知"</li>
                  </ul>
                </div>
                <div>
                  <h3 className="font-medium text-gray-800 mb-2">Runtime 调试:</h3>
                  <ul className="list-disc list-inside space-y-1">
                    <li>在 <code className="bg-gray-100 px-1 rounded">CopilotKit/packages/runtime</code> 中设置断点</li>
                    <li>特别关注 <code className="bg-gray-100 px-1 rounded">copilot-runtime.ts</code> 的 <code className="bg-gray-100 px-1 rounded">processRuntimeRequest</code> 方法</li>
                    <li>查看消息处理、Action 执行等核心流程</li>
                  </ul>
                </div>
                <div>
                  <h3 className="font-medium text-gray-800 mb-2">Vite 特性:</h3>
                  <ul className="list-disc list-inside space-y-1">
                    <li>🚀 快速热重载 (HMR)</li>
                    <li>⚡ 极快的启动速度</li>
                    <li>📦 优化的构建输出</li>
                    <li>🔧 简化的配置</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </main>
      </div>

      {/* CopilotKit 聊天界面 */}
      <CopilotSidebar
        labels={{
          title: "AI 助手 (DeepSeek + Vite 调试模式)",
          initial: "👋 你好！我是 CopilotKit + DeepSeek + Vite 调试助手。\n\n🔧 **调试功能:**\n- 使用 DeepSeek Chat 模型\n- 基于 Vite 构建，启动更快\n- 可以执行多种自定义 Actions\n- 后端使用 Express + CopilotKit Runtime + DeepSeek Adapter\n- 你可以在代码中设置断点进行调试\n\n💡 **试试问我:**\n- \"现在几点了？\"\n- \"计算 10 + 20 * 3\"\n- \"查询用户1的信息\"\n- \"获取运行时状态\"\n- \"显示一个通知\"\n\n⚡ **Vite 优势:**\n- 极快的热重载\n- 更快的开发体验\n\n让我们开始调试吧！",
          placeholder: "输入消息进行调试...",
        }}
        defaultOpen={true}
        clickOutsideToClose={false}
        className="w-96 border-l"
      />
    </div>
  );
} 