import { CopilotKit } from "@copilotkit/react-core";
import HomePage from "./components/HomePage";

function App() {
  const backendUrl = import.meta.env.VITE_BACKEND_URL || 'http://localhost:3001';

  return (
    <CopilotKit 
      runtimeUrl={`${backendUrl}/api/copilotkit`}
      publicApiKey=""
    >
      <HomePage />
    </CopilotKit>
  );
}

export default App; 